import { useState } from "react";
import "./newProduct.css";
import {
  getStorage,
  ref,
  uploadBytesResumable,
  getDownloadURL,
} from "firebase/storage";
import app from "../../firebase";
import { addProduct } from "../../redux/apiCalls";
import { useDispatch, useSelector } from "react-redux";
import { userRequestFormData } from "../../requestMethods";

export default function NewProduct() {
  const [inputs, setInputs] = useState({});
  const [file, setFile] = useState(null);
  const [cat, setCat] = useState("seeds");
  const [success, setSuccess] = useState(true);
  const dispatch = useDispatch();

  const subCategoryMap = {
    seeds: ["summer", "winter"],
    chemicals: ["insecticide", "pesticide", "herbicide", "fungicide"],
    extras: ["mulching", "cocopit"],
  };

  const handleChange = (e) => {
    setInputs((prev) => {
      return { ...prev, [e.target.name]: e.target.value };
    });
    if (e.target.name === "category") {
      console.log(e.target.value);
      setCat(e.target.value);
    }
  };

  const handleClick = async (e) => {
    e.preventDefault();
    console.log(inputs);

    const formData = new FormData();
    Object.entries(inputs).forEach(([key, value]) => {
      console.log(key, value);
      formData.append(key, value);
    });

    formData.append("productImage", file);
    setSuccess(true);
    try {
      const response = await userRequestFormData.post("/products", formData);
      console.log(response);
    } catch (e) {
      console.log(e);
      setSuccess(false);
    }
  };

  return (
    <div className="newProduct">
      <h1 className="addProductTitle">New Product</h1>
      {success === false && (
        <span style={{ color: "red", fontSize: "18px" }}>Upload Failed</span>
      )}
      <form className="addProductForm">
        <div className="addProductItem">
          <label>Image</label>
          <input
            type="file"
            id="file"
            onChange={(e) => setFile(e.target.files[0])}
          />
        </div>
        <div className="addProductItem">
          <label>Title</label>
          <input
            name="title"
            type="text"
            placeholder="Apple Airpods"
            onChange={handleChange}
          />
        </div>
        <div className="addProductItem">
          <label>Description</label>
          <input
            name="desc"
            type="text"
            placeholder="description..."
            onChange={handleChange}
          />
        </div>
        <div className="addProductItem">
          <label>Price</label>
          <input
            name="price"
            type="number"
            placeholder="100"
            onChange={handleChange}
          />
        </div>
        <div className="addProductItem">
          <label>Category</label>
          <select name="category" onChange={handleChange}>
            <option value="seeds">Seeds</option>
            <option value="chemicals">Chemicals</option>
            <option value="extras">Munching and Cocopit</option>
          </select>
        </div>
        <div className="addProductItem">
          <label>SubCategory</label>
          <select name="subcategory" onChange={handleChange}>
            <option disabled selected>
              Sub Category
            </option>
            {subCategoryMap[cat].map((item) => {
              return <option value={item}>{item}</option>;
            })}
          </select>
        </div>
        <div className="addProductItem">
          <label>Stock</label>
          <select name="inStock" onChange={handleChange}>
            <option value="true">Yes</option>
            <option value="false">No</option>
          </select>
        </div>
        <button onClick={handleClick} className="addProductButton">
          Create
        </button>
      </form>
    </div>
  );
}
