import { Link, useLocation } from "react-router-dom";
import "./product.css";
import Chart from "../../components/chart/Chart";
import { productData } from "../../dummyData";
import { Publish, CancelOutlined } from "@material-ui/icons";
import { useSelector, useDispatch } from "react-redux";
import { useEffect, useMemo, useState } from "react";
import { userRequest } from "../../requestMethods";
import { updateProduct } from "../../redux/apiCalls";
import { IMAGEURL } from "../../Constant";

export default function Product() {
  const location = useLocation();
  const productId = location.pathname.split("/")[2];
  const [inputs, setInputs] = useState({});
  const [file, setFile] = useState(null);

  const dispatch = useDispatch();

  const product = useSelector((state) =>
    state.product.products.find((product) => product._id === productId)
  );
  const subCategoryMap = {
    seeds: ["summer", "winter"],
    chemicals: ["insecticide", "pesticide", "herbicide", "fungicide"],
    extras: ["mulching", "cocopit"],
  };
  const [cat, setCat] = useState(product.category);

  const handleChange = (e) => {
    setInputs((prev) => {
      return { ...prev, [e.target.name]: e.target.value };
    });
    if (e.target.name === "category") {
      console.log(e.target.value);
      setCat(e.target.value);
    }
    // console.log(inputs);
  };
  const handleClick = (e) => {
    e.preventDefault();
    const formData = new FormData();
    Object.entries(inputs).forEach(([key, value]) => {
      console.log(key, value);
      formData.append(key, value);
    });
    formData.append("productImage", file);
    updateProduct(productId, formData, dispatch);
  };
  return (
    <div className="product">
      <div className="productTitleContainer">
        <h1 className="productTitle">Product</h1>
        <Link to="/newproduct">
          <button className="productAddButton">Create</button>
        </Link>
      </div>
      <div className="productTop">
        <div className="productTopRight">
          <div className="productInfoTop">
            <img
              src={IMAGEURL + product.thumb}
              alt=""
              className="productInfoImg"
            />
            <span className="productName">{product.title}</span>
          </div>
          <div className="productInfoBottom">
            <div className="productInfoItem">
              <span className="productInfoKey">id:</span>
              <span className="productInfoValue">{product._id}</span>
            </div>
            <div className="productInfoItem">
              <span className="productInfoKey">sales:</span>
              <span className="productInfoValue">{product.desc}</span>
            </div>
            <div className="productInfoItem">
              <span className="productInfoKey">in stock:</span>
              <span className="productInfoValue">
                {product.inStock ? "Yes" : "No"}
              </span>
            </div>
          </div>
        </div>
      </div>
      <div className="productBottom">
        <form className="productForm">
          <div className="productFormLeft">
            <label>Product Name</label>
            <input
              type="text"
              placeholder={product.title}
              name="title"
              onChange={handleChange}
            />
            <label>Product Description</label>
            <input
              type="text"
              placeholder={product.desc}
              name="desc"
              onChange={handleChange}
            />
            <label>Price</label>
            <input
              type="text"
              name="price"
              placeholder={product.price}
              onChange={handleChange}
            />
            <label>In Stock</label>
            <select name="inStock" id="idStock" onChange={handleChange}>
              <option value="true">Yes</option>
              <option value="false">No</option>
            </select>
            <label>Category</label>
            <select
              name="category"
              onChange={handleChange}
              defaultValue={product.category}
            >
              <option value="seeds">Seeds</option>
              <option value="chemicals">Chemicals</option>
              <option value="extras">Munching and Cocopit</option>
            </select>
            <label>SubCategory</label>
            <select
              name="subcategory"
              onChange={handleChange}
              defaultValue={product.subcategory}
            >
              <option disabled selected>
                Sub Category
              </option>
              {subCategoryMap[cat].map((item) => {
                return <option value={item}>{item}</option>;
              })}
            </select>
          </div>
          <div className="productFormRight">
            <div className="productUpload">
              <img
                src={IMAGEURL + product.thumb}
                alt=""
                className="productUploadImg"
              />
              <label for="file">
                <Publish />
              </label>
              <input
                type="file"
                id="file"
                style={{ display: "none" }}
                onChange={(e) => {
                  setFile(e.target.files[0]);
                }}
              />

              {file && (
                <span
                  style={{
                    alignItems: "center",
                    textAlign: "center",
                    fontSize: "14px",
                  }}
                >
                  {file.name} {"  "}{" "}
                  <CancelOutlined
                    style={{ marginLeft: "10px" }}
                    onClick={() => {
                      setFile(null);
                    }}
                  />
                </span>
              )}
            </div>
            <button className="productButton" onClick={handleClick}>
              Update
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
